/* Button.css */
.custom-button {
    background-color: #98C5BD;
    color: white;
    border: none;
    padding: 10px 20px;
    font-size: 16px;
    cursor: pointer;
    border-radius: 5px;
    transition: background-color 0.3s ease;
    font-family: 'DM-Sans', sans-serif;
}

.custom-button:hover {
    background-color: #6bc2b2;
}

.custom-button:focus {
    outline: none;
}
